export default {
	data() {
		return {
			maxlength: 6,
			value: '',
			second: 60,
			show: false,
			error: false,
			phone: '',
			redirctUrl: ''
		};
	},
	computed: {
		hidePhone() {
			return this.phone.substring(0, 3) + ' **** ' + this.phone.substring(7, 11)
		}
	},
	onLoad(opt) {
		this.phone = opt.phone;
		this.redirctUrl = opt.redirct;
		let interval = setInterval(() => {
			this.second--;
			if (this.second <= 0) {
				this.show = true;
				if (this.value.lenth != 4) {
					this.error = true;
				}
				clearInterval(interval);
			}
		}, 1000);
	},
	methods: {
		/**
		 * change事件侦听
		 * @param {Object} value
		 */
		change(value) {
			console.log('change', value);
		},
		/**
		 * 输入完验证码最后一位执行
		 * @param {Object} value
		 */
		finish(value) {
			this.$u.api.login({
				telephone: parseInt(this.phone),
				authCode: value
			}).then((res) => {
				const {
					data
				} = res;
				getApp().globalData.userId = this.phone;
				uni.setStorageSync('user-info', {
					token: `${data.tokenHead} ${data.token.token}`,
					phone: data.name,
					id: data.id
				});
				const __url = this.$u.test.isEmpty(this.redirctUrl) ? 'pages/index/index' : this.redirctUrl;
				this.$u.route({
					type: 'redirectTo',
					url: __url,
					params: {
						phone: this.phoneVal
					}
				})
			})
		}
	}
};
